.page {
  font-family: Poppins, sans-serif;
  font-size: 23px;
  line-height: 140%;
  color: white;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &_menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &__menu:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

h2 {
  margin: 0;
}
